import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { StaticContext } from 'react-router';
import Markdown from 'react-markdown';
import { Route, Redirect, Link, Switch, RouteComponentProps } from 'react-router-dom';

import * as regexes from '../utils/regexes';
import styles from '../css/home.module.css';
import { GET, socket } from '../utils/requests';
import { Notif, TournamentData, ProfileData } from '../interfaces';
import { FaFacebook, FaTwitter, FaTwitch, FaAngleRight, FaGithub, FaAngleLeft } from 'react-icons/fa';
import { GoPrimitiveDot } from 'react-icons/go'
import { MdAnnouncement } from 'react-icons/md';
import { capitalise } from '../utils/prototype';
import { parseHtml, RouterLink } from '../utils/plugins';

export default function Profile(props: RouteComponentProps<any, StaticContext, any> & {
	profile: ProfileData
}) {

	const [tournaments, setTournaments] = useState([] as TournamentData[]);
	const [notifications, setNotifications] = useState([] as Notif[]);
	const [selected, select] = useState({} as Notif);
	const animate = useMemo(() => props.history.location.state && props.history.location.state.from, [props.history.action]);

	const updateTournaments = useCallback(() => GET({ url: '/tournaments' })
		.then(setTournaments), [setTournaments]);

	const updateNotifications = useCallback(() => GET({ url: '/notifications' })
			.then((n) => setNotifications(n.sort((a: Notif, b: Notif) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf()))), [setNotifications]);

	useEffect(() => {
		updateTournaments();
		updateNotifications();
	}, [updateTournaments, updateNotifications]);
	return (<>
        <div id='main' className={animate ? 'animated-left' : ''}>
			<div className={styles.container}>
				<div className={styles.title}>
					<h1><span className='bold'>{props.profile.username}</span></h1>
				</div>
				<div className={[styles.section, styles.yourTournaments].join(' ')}>
					<table className='download-table' id='profileTournaments'>
						<thead>
							<tr>
								<th className={styles.sectionHeader} colSpan={5}>
									Your Tournaments
								</th>
							</tr>
							<tr>
								<th>Manage</th>
								<th>Name</th>
								<th>Started</th>
								<th>Round</th>
								<th>View</th>
							</tr>
						</thead>
						<tbody>
							{tournaments.map((tourn, i) => {
								let dateStarted = new Date(tourn.createdAt).toString();
								return (
									<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
										<td><a className={styles.tournamentLink} href='https://beta.scorchapp.co.uk'>
											<FaAngleLeft />
										</a></td>
										<td>
											{tourn.name || tourn.id}
										</td>
										<td><Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
											{dateStarted.slice(0, 11)}
										</Link></td>
										<td><Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
											{tourn.round}
										</Link></td>
										<td><Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
											<FaAngleRight />
										</Link></td>
									</tr>
								);
							})}
							<tr><th colSpan={4}>{'\u200b'}</th></tr>				
						</tbody>
					</table>
				</div>
				<div>
					<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={['header-button', styles.new].join(' ')}>
						Create New Tournament
						<FaAngleRight />
					</a>
				</div>
			</div>				
		</div>
		{(() => {
			let n = selected;
			if (!n.id) return null;
			let icon = <MdAnnouncement />;
			let domain = (n.link || '').match(regexes.link);
			if (domain) switch (domain[1]) {
				case 'facebook.com': {
					icon = <FaFacebook />;
					break;
				}
				case 'twitter.com': {
					icon = <FaTwitter />;
					break;
				}
				case 'twitch.tv': {
					icon = <FaTwitch />;
					break;
				}
				case 'github.com': {
					icon = <FaGithub />;
					break;
				}
				default: {
					break;
				}
			}
			return <div className={styles.lightBox} onClick={() => select({} as Notif)}>
				<div className={[styles.section].join(' ')}>
					<div className={styles.lightBoxTitle}>
						<div>
							{n.link ? <a className={styles.iconLink} href={n.link} rel='noopener noreferrer' target='_blank'>
								{icon}
							</a> :
							<div>icon</div>}
						</div>
						<div>{selected.title}</div>
					</div>
					<div className={styles.lightBoxContent} onClick={(e) => e.stopPropagation()}>
						<Markdown
							source={n.content.trim() + (n.link ? `\n\n[${n.link}](${n.link})` : '')}
							escapeHtml={false}
							astPlugins={[parseHtml]}
							renderers={{
								link: RouterLink
							}}
						/>
					</div>
					<div className={styles.dateTime}>
						{capitalise(n.role)} Announcement | {new Date(n.createdAt).toString().slice(0, 21)}
					</div>
				</div>
			</div>
		})()}
	</>)
}