import React, { ReactElement, useMemo, useCallback, useReducer } from 'react';
import { TournamentStatus, Player, TournamentSettings } from '../interfaces';

import styles from '../css/crosstable.module.css';

interface TableProps {
	setDisplayPlayer: (id: string) => void
	show: () => void
	status: TournamentStatus
	players: Player[]
	settings: TournamentSettings
	round: number
}

interface ColumnData {
	id?: (entry: Player, i: number) => string
	name: string | ((key: string, i: number) => string)
	className: 'blank' | 'playerName' | 'locked' | 'rating' | 'unset' | 'edit' | ((v: string, i: number, settings: TournamentSettings) => string),
	sort?: false | ((a: any, b: any) => number),
	show?: (entry: Player, i: number, settings: TournamentSettings) => boolean
	value: (entry: Player, i: number, settings: TournamentSettings) => any
	convert?: (v: any, p: Player, s: TournamentSettings) => string
	postConvert?: (v: string, i: number) => string
	mobile?: string
}

const defaultSorts = ['score', 'gamePoints', 'rating', 'name']

export default function CrossTable(props: TableProps): ReactElement {

	const columns = useMemo(() => {
		return {
			pre: {
				id: {
					mobile: 'Rank',
					name: '',
					className: 'blank',
					sort: false,
					value: (): number => 0,
					postConvert: (v: string, i: number): string => (1 + i).toString()
				},
				name: {
					name: 'Name',
					className: 'playerName',
					id: (p: Player): string => p.id,
					sort: (a: string, b: string) => {
						if (a > b) return 1;
						if (a < b) return -1;
						return 0;
					},
					value: (p: Player, i: number, s: TournamentSettings): string => s.competitors === 'individual' ? [p.firstName, p.lastName].join(' ') : p.firstName
				},
				rating: {
					name: 'Rating',
					className: 'locked',
					value: (p: Player): number => p.rating,
					show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
				}
			},
			roundColumn: {
				name: (key: string, i: number): string => key.slice(-1),
				className: (v: string, i: number, s: TournamentSettings) => {
					if (v === '*') return 'edit';
					if (v === '') return 'unset';
					else return 'locked';
				},
				value: () => {},
				convert: (v: string, p: Player, s: TournamentSettings) => {
					if (v === '') return '';
					else if (v === '*') return '*';
					let nb = parseFloat(v);
					let output: string;
					if (nb === 0.5) output = '';
					else output = nb.toString();
					if (v.endsWith('b')) output += 'b';
					return output;
				},
				sort: (a: string, b: string): number => {
					if (a === b) return 0;
					if (a === '') return -1;
					else if (b === '') return -1;
					if (a === '*') return 1;
					else if (b === '*') return -1;
					let [aN, bN] = [parseInt(a), parseInt(b)];
					if (a.toString().endsWith('b')) aN -= 0.001;
					if (b.toString().endsWith('b')) bN -= 0.001;
					return bN - aN;
				}
			},
			post: {
				score: {
					mobile: 'Match Points',
					name: 'MP',
					className: 'locked',
					value: (p: Player) => p.histories.slice(1, props.round + 1).reduce((acc, curr) => curr ? acc += curr.match || 0 : acc, 0),
					convert: (v: number, p: Player): string => Math.floor(v) === v ? v.toFixed() : v === 0.5 ? '' : Math.floor(v).toFixed() + ''
				},
				gamePoints: {
					mobile: 'Game Points',
					name: 'GP',
					className: 'locked',
					value: (p: Player) => p.histories.slice(1, props.round + 1).reduce((acc, curr) => curr ? acc += curr.game || 0 : acc, 0),
					convert: (v: number): string => v.toFixed(1)
				},
				gameDifference: {
					mobile: 'Game Point Difference',
					name: 'G. +/-',
					className: 'locked',
					value: (p: Player) => {
						let matchPoints = 0;
						let played = 0;
						p.histories.slice(1, props.round + 1).forEach((curr) => {
							if (!curr) return;
							matchPoints += curr.game || 0;
							played += curr.played
						});
						return matchPoints * 2 - played;
					},
					convert: (v: number): string => {
						if (!v) return '';
						if (v > 0) return '+' + v.toFixed(1);
						else return v.toFixed(1)
					},
				},
				matchPercentage: {
					mobile: 'Match Point Percentage',
					name: 'M. %',
					className: 'rating',
					value: (p: Player, round: number) => {
						let matchPoints = 0;
						let played = 0;
						p.histories.slice(1, round + 1).forEach((curr) => {
							if (!curr) return;
							matchPoints += curr.match || 0;
							played += 1 || 0;
						});
						if (played === 0) return -1;
						return matchPoints * 100 / played;
					},
					convert: (v: number): string => v.toFixed()
				},
				gamePercentage: {
					mobile: 'Game Point Percentage',
					name: 'G. %',
					className: 'rating',
					value: (p: Player, round: number) => {
						let gamePoints = 0;
						let played = 0;
						p.histories.slice(1, round + 1).forEach((curr) => {
							if (!curr) return;
							gamePoints += curr.game || 0;
							played += curr.played || 0;
						});
						if (played === 0) return -1;
						return gamePoints * 100 / played;
					},
					convert: (v: number): string => v.toFixed()
				},
				played: {
					mobile: 'Played',
					name: 'P',
					className: 'locked',
					value: (p: Player) => p.histories.slice(1, props.round + 1).reduce((acc, curr) => curr ? acc += curr.played || 0 : acc, 0),
					convert: (v: number): string => v.toString()
				},
				performanceRating: {
					mobile: 'Performance Rating',
					name: 'Perf',
					className: 'rating',
					value: (p: Player) => p.performanceRating,
					convert: (v: number): string => v.toFixed(),
					show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
				},
				ratingDifference: {
					mobile: 'Performance Rating Difference',
					name: 'P. +/-',
					className: 'rating',
					value: (p: Player): number => {
						if (!p.performanceRating) return 0;
						return p.performanceRating - p.rating;
					},
					convert: (v: number): string => {
						if (!v) return '';
						if (v > 0) return '+' + v.toString();
						else return v.toString()
					},
					show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
				},
				expectedScore: {
					mobile: 'Expected Score',
					name: 'Expected',
					className: 'rating',
					value: (p: Player): number => p.expectedScore,
					convert: (v: number, p: Player): string => v.toFixed(1) + ' / ' + p.played,
					show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
				},
				expectedDifference: {
					mobile: 'Score to Expected',
					name: 'E. +/-',
					className: 'rating',
					value: (p: Player): number => p.score - p.expectedScore,
					convert: (v: number): string => {
						if (typeof v === 'undefined') return '';
						if (v > 0) return '+' + v.toFixed(2);
						else return v.toFixed(2)
					},
					show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
				},
			}
		} as {
			pre: {
				[key: string]: ColumnData
			},
			roundColumn: ColumnData
			post: {
				[key: string]: ColumnData
			}

		}
	}, [props.round]);

	const totalRounds = useMemo((): number => {
		let totalRounds: number;
		if (props.status.round > props.settings.totalRounds) totalRounds = props.status.round;
		else if (props.settings.totalRounds === 'Infinity') totalRounds = props.status.round;
		else totalRounds = props.settings.totalRounds;
		return totalRounds;
	}, [props.status.round, props.settings.totalRounds]);

	const roundHeaders = useMemo(() => {
		let h = [] as string[];
		for (let i = 1; i <= totalRounds; i++) {
			h.push('round.' + i.toString());
		}
		return h;
	}, [totalRounds]);

	const headers: string[] = useMemo(() => [...Object.keys(columns.pre), ...roundHeaders, ...Object.keys(columns.post)], [roundHeaders])
		.filter((key, i) => {
			let meta = columns.pre[key] || columns.post[key];
			if (!meta) return true;
			if (meta.show && !meta.show(null as any as Player, i, props.settings)) return false;
			return true;
		});

	const setSortHeader = useCallback((state: [string, number], action: string): [string, number] => {
		if (!headers.includes(action)) return ['', 0];
		if (state[0] !== action) return [action, 0];
		else return [action, state[1] + 1];
	}, [headers]);
	const [sort, sortBy] = useReducer(setSortHeader, ['score', 0])

	const renderRounds = useCallback((p: Player) => {
		let scores = [''] as string[];
		for (let i = 1; i <= totalRounds; i++) {
			if (i > props.round || !p.histories[i]) {
				scores[i] = '';
				continue;
			};
			if (p.histories[i].match === null || p.histories[i].match === undefined) scores[i] = '*';
			else scores[i] = (p.histories[i].match as number).toString();
			if (p.histories[i].id === 'bye') scores[i] += 'b';
		}
		return scores.slice(1);
	}, [totalRounds, props.round]);	

	const renderRows: JSX.Element[] = useMemo(() => {
		let rowsData = [] as [number, ColumnData, Player][][];
		for (let i = 0; i < props.players.length; i++) {
			let p = props.players[i];
			if (p.id === 'bye') continue;
			let pres = [] as [number, ColumnData, Player][];
			for (let [k, meta] of Object.entries(columns.pre)) {
				if (meta.show && !meta.show(p, i, props.settings)) continue;
				pres.push([meta.value(p, i, props.settings), meta, p]);
			}
			let rounds = renderRounds(p).map(entry => [entry as any as number, columns.roundColumn, p]) as [number, ColumnData, Player][];
			let post = [] as [number, ColumnData, Player][];
			for (let [k, meta] of Object.entries(columns.post)) {
				if (meta.show && !meta.show(p, i, props.settings)) continue;
				post.push([meta.value(p, i, props.settings), meta, p]);
			}
			let rowData = [...pres, ...rounds, ...post];
			rowsData.push(rowData);
		}

		let sortFunc = (a: number, b: number): number => b - a;
		let sortMeta = sort[0].startsWith('round') ? columns.roundColumn : (columns.pre[sort[0]] || columns.post[sort[0]]);
		if (sortMeta && sortMeta.sort !== false) {
			if (sortMeta.sort) sortFunc = sortMeta.sort;
			let sortCol = headers.indexOf(sort[0]);
			if (sortCol !== -1)
			rowsData = rowsData.sort((a, b): number => {
				let x: number;
				if (sort[1] % 2) x = sortFunc(b[sortCol][0], a[sortCol][0]);
				else x = sortFunc(a[sortCol][0], b[sortCol][0]);

				let defSort = defaultSorts.slice(0);
				while (x === 0 && defSort[0]) {
					let s = defSort.shift() as string;
					let newSortCol = headers.indexOf(s);
					if (newSortCol === sortCol || newSortCol < 0) continue;
					let newSortMeta = (columns.pre[s] || columns.post[s]);
					let newSortFunc = newSortMeta.sort || sortFunc;
					if (sort[1] % 2) x = newSortFunc(b[newSortCol][0], a[newSortCol][0]);
					else x = sortFunc(a[newSortCol][0], b[newSortCol][0]);
				}
				return x;
			});
		}

		let rows = rowsData.map((rowData, i) => {
			return (
				<tr key={'row.' + i} className={styles.tr}>
					{rowData.map(([value, meta, p]: [number, ColumnData, Player], j: number) => {
						let display = value ? value.toString() : '';
						if (meta.convert) display = meta.convert(value, p, props.settings);
						else if (meta.postConvert) display = meta.postConvert(display, i);
						if (typeof display === 'undefined' || display === '') display = '\u200b';
						let className = typeof meta.className === 'function' ? meta.className(display, j, props.settings) : meta.className
						return <>
							<th className={[styles.cell, styles.headerCell].join(' ')} key={['cellHeader', j].join('.')}>
								<div>
									{meta.mobile ? meta.mobile : typeof meta.name === 'function' ? meta.name((headers[j] || '').toString(), j) : meta.name}
								</div>
							</th>
							<th key={['cellBody', j].join('.')}>							
								<div
									id={meta.id ? meta.id(p, j) : undefined}
									key={[value, j].join('.')}
									className={[styles.cell, className, styles[className]].join(' ')}
								>
									{display}
								</div>
							</th>
						</>;
					})}
				</tr>
			);
		})
		return rows;
	}, [props.players, props.settings, renderRounds, sort]);
		
	return (
		<section>
			<table className={['download-table full-width', styles.table, 'standings'].join(' ')}>
				<thead className={styles.headers}>
					<tr>
						{headers.map((key, i) => {
							let meta = key.startsWith('round') ? columns.roundColumn : (columns.pre[key] || columns.post[key]);
							return <th
								scope='column'
								key={['column', i].join('.')}
								className={[styles.th, key.startsWith('round') ? styles.round : ''].join(' ')}
								id={key}
								onClick={() => sortBy(key)}
							>
								<div className={[styles.cell, styles.headerCell].join(' ')}>
									{typeof meta.name === 'function' ? meta.name(key, i) : meta.name}
								</div>
							</th>;
						})}
					</tr>
				</thead>
				<tbody>						
					{renderRows}
				</tbody>
			</table>
		</section>
	);
}